import React from 'react';
import { string, shape, bool } from 'react-proptypes';

import { SignupForm } from '../../../components/SignupForm';

import styles from './MainBlock.module.scss';

const MainBlock = ({ params, previewing }) => (
  <div className={styles.mainBlock__container}>
    <h1>{params && params.title}</h1>

    <p
      className={`${styles.mainBlock__description} subline2`}
      dangerouslySetInnerHTML={{ __html: params && params.description }}
    />

    <SignupForm
      buttonText={params && params.buttonText}
      hasAutofocus={true}
      disabled={previewing}
      inputPlaceholder={params && params.inputPlaceholder}
    />
  </div>
);

MainBlock.propTypes = {
  params: shape({
    title: string,
    description: string,
    inputPlaceholder: string,
    buttonText: string
  }),
  previewing: bool
};

MainBlock.defaultProps = {
  params: {},
  previewing: false
};

export default MainBlock;



