import React from 'react';
import { arrayOf, string, shape } from 'react-proptypes';

import styles from './Features.module.scss';

const Feature = ({ text, icon, color }, index) => (
  <section key={index} className={styles.feature__container}>
    <div className={styles.feature__content}>
      <i className='material-icons' style={{ color }}>
        {icon}
      </i>
      <p className='copytext-large'>{text}</p>
    </div>
  </section>
);

const Features = ({ params }) => (
  <div className={styles.features__container}>
    <h2>{params && params.title}</h2>

    <div className={styles.items__container}>
      {params && params.items && params.items.map((item, index) => Feature(item, index))}
    </div>
  </div>
);

Features.propTypes = {
  params: shape({
    title: string,
    items: arrayOf(shape({
      title: string,
      icon: string,
      color: string,
      text: string
    }))
  })
};

Features.defaultProps = {
  params: {
    items: []
  }
};

export default Features;



