import React from 'react';
import { string, shape } from 'react-proptypes';

import styles from './ExtraBlock.module.scss';

const ExtraBlock = ({ params }) => (
  <section className={styles.extraBlock__container}>
    <h2>{params && params.title}</h2>

    <p className={`${styles.extraBlock__description} subline2`}>
      {params && params.description}
    </p>
  </section>
);

ExtraBlock.propTypes = {
  params: shape({
    title: string,
    description: string,
    action: string
  })
};

export default ExtraBlock;



